<template>
  <div class="login-container login-special" :style="bgStyle">

    <!--头部-->
    <el-header height="80px">
      <!--Logo和信息-->
      <div class="info-container">
        <!--左侧logo-->
        <div class="info-logo">
          <img v-if="siteInfo.logo" :src="siteInfo.logo" class="header-logo-left" alt="">
          <img v-else src="../../assets/images/login/noimg.png" class="header-logo-left" alt="">
        </div>
        <!--右侧 客服和微信二维码-->
        <div class="info-service">
          <div class="info-service-line" v-show="info.servicePhone"><span>客服热线：{{info.servicePhone}}</span></div>
          <el-popover
            placement="top-start"
            width="200"
            trigger="hover">

            <div style="text-align: center; margin: 0 auto">
              <img :src="getImg(info.wechatQrcode)" class="max-img" alt="">
            </div>

            <div slot="reference" class="info-service-wechat">
              <img src="../../assets/images/login/weixin.png" class="wxlogo" alt="">
              <span class="wxlogo-tips">关注微信公众号</span>
            </div>

          </el-popover>

        </div>
      </div>
    </el-header>

    <!--登录区-->
    <el-main class="main">
      <!--登录框-->
      <div class="login-widnow-box flex-c-c-c">
        <h1 class="login-window-title">代理商后台管理系统</h1>
        <div class="login-window-form flex-c-c-c">

          <div class="login-window-decorateline flex-c-c-c">
            <span>用户登录</span>
          </div>

          <div class="login-window-input-form flex-c-c-c">

            <!--用户名-->
            <div class="login-window-input userinput flex-r-c-c">
              <div class="username-icon input-icon">
                <img src="../../assets/images/login/new/联系人.png" alt="">
              </div>
              <el-input class="input-item" v-model.trim="loginForm.username"></el-input>
            </div>
            <!--密码-->
            <div class="login-window-input passwordinput flex-r-c-c">
              <div class="password-icon input-icon">
                <img src="../../assets/images/login/new/密码.png" alt="">
              </div>
              <el-input type="password" class="input-item" @keyup.enter.native="handleLogin"
                        v-model.trim="loginForm.password"></el-input>
            </div>

            <div class="fogotPW-btn-box">
              <span @click="goFogotPw">忘记密码？</span>
            </div>

            <div class="login-btn-box flex-c-c-c" @click="handleLogin">
              <span>{{loading?'登录中...':'登录'}}</span>
            </div>

          </div>


        </div>

      </div>
    </el-main>

    <!--页脚-->
    <el-footer height="80px" class="footer">

      <div class="copyright">
        <p v-show="info.techSupport">技术支持&nbsp{{info.techSupport||''}}</p>
        <p><span v-show="info.icpInfo">网站ICP备案号：{{info.icpInfo||''}}</span>
          <span v-show="info.copyrightInfo">Copyright&nbsp{{info.copyrightInfo||''}}</span></p>
      </div>


    </el-footer>

  </div>
</template>

<script>
  import {getSiteInfo} from '@/modules/login/api/login'
  import {returnThumbnailUrl, returnFilesUrl, uploadUrl} from '@/modules/file/api/upload' //图片操作API
  // import QRCode from 'qrcode'
  export default {
    name: 'login',
    data() {
      return {
        loginForm: {
          username: '',
          password: ''
        },
        siteInfo: {
          bg: '',
          logo: '',
        },
        bgStyle: {
          background: '',
          backgroundSize: '',
        },
        loading: false,
        pwdType: 'password',
        info: {}
      }
    },
    mounted() {
      this.getInfo();
    },
    methods: {
      showPwd() {
        if (this.pwdType === 'password') {
          this.pwdType = ''
        } else {
          this.pwdType = 'password'
        }
      },
      handleLogin() {
        this.loading = true;
        this.$store.dispatch('login', this.loginForm).then(() => {
          // 登陆成功，获取别名。
          this.$store.dispatch('getAliasInfo').then(() => {
            //获取别名成功，开始跳转首页
            this.$router.replace({path: '/'})
          }).catch(() => {
            this.$router.replace({path: '/'});
            this.loading = false
          });
        }).catch(() => {
          this.loading = false
        })

      },

      /**
       * 获取信息
       */
      getInfo: function () {
        getSiteInfo().then(res => {
          console.log(res);
          let data = res.obj;
          //如果有背景图
          if (data.bgmPhoto !== '' && data.bgmPhoto != null) {
            this.siteInfo.bg = "url('" + this.getImg(data.bgmPhoto) + "') no-repeat";
            this.bgStyle.background = this.siteInfo.bg; //背景图片
            // this.bgStyle.backgroundSize = 100 + '%' //100%大小
          }
          //如果有LOGO
          if (data.logo !== '' && data.logo != null) {
            this.siteInfo.logo = this.getImg(data.logo);
            sessionStorage.removeItem('logoImg');
            sessionStorage.setItem('logoImg', this.getImg(data.logo))
          }
          this.info = data;
          document.title = this.info.name + '-' + '代理商平台' || '代理商'
        })
      },

      /**
       * 换取图片
       */
      getImg: function (val) {
        let reg = /^[0-9]*$/;
        if (reg.test(val)) {
          return returnFilesUrl(val)
        } else {
          let base64 = 'data:image/png;base64,';
          return base64
        }
      },


      /**
       * 忘记密码
       */
      goFogotPw() {
        console.log('go');
        this.$router.push({path: '/fogot'})
      },

    }
  }
</script>


<style rel="stylesheet/scss" lang="scss" scoped>

  $bg: #2d3a4b;
  $dark_gray: #889aa4;
  $light_gray: #eee;

  .flex-c-c-c {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .flex-r-c-c {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .login-container {
    height: 100%;
    width: 100%;

    color: white;

    /*默认背景*/
    background: url("../../assets/images/login/new/背景1.png") no-repeat center center;
    background-size: 100% 100%;

    /*Logo和信息*/
    .info-container {
      padding: 20px 30px;
      height: 120px;

      font-size: 14px;
      color: white;

      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;

      /*logo*/
      .info-logo {
        height: 80px;

        .header-logo-left {
          height: 80px;
          width: auto;
        }
      }

      /*联系和微信*/
      .info-service {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        cursor: default;

        .info-service-line {

        }


        .info-service-wechat {
          margin-left: 20px;
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;


          .wxlogo {
            width: 23px;
            height: 20px;
          }

          .wxlogo-tips {
            margin-left: 5px;
          }
        }


      }

    }


    //中间 登录区

    .main {
      height: calc(100% - 120px);
      padding: 0 252px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-end;

    }

    @media (max-width: 1024px) {
      .main {
        padding: unset;
        align-items: center;
      }
    }


    .login-widnow-box {

      ::-webkit-scrollbar {
        display: none;
      }


      /*登录标题*/
      .login-window-title {
        font-size: 36px;
        margin: 10px 0;
        line-height: 40px;
        font-family: Microsoft YaHei;
        font-weight: bold;
        color: rgba(255, 255, 255, 1);
      }


      /*登录表单*/
      .login-window-form {
        margin: 20px 0 40px 0;
        width: 593px;
        height: 383px;

        background: url("../../assets/images/login/new/背景框.png");

        @media (max-width: 594px) {
          width: unset;
          background: unset;
        }

        @media (max-height: 600px) {
          height: unset;
        }

        /*登录表单 内 横线装饰*/
        .login-window-decorateline {
          width: 380px;
          height: 10px;

          background: url("../../assets/images/login/new/登录装饰.png");

          span {
            font-size: 16px;
            font-weight: 400;
            color: rgba(255, 255, 255, 1);
            line-height: 16px;
          }
        }

        /*登录表单 内 表单*/
        .login-window-input-form {
          margin-top: 35px;

          .login-window-input {
            width: 387px;
            height: 50px;

            background: url("../../assets/images/login/new/输入框.png");
          }

          .input-item {
            position: relative;
            left: 36px;

            font-size: 16px;
            font-family: Microsoft YaHei;
            font-weight: 400;
            color: rgb(198, 198, 198) !important;
            line-height: 16px;
          }

          .passwordinput {
            margin-top: 27px;
          }


          .input-icon {
            position: relative;
            top: 4px;
            left: 30px;
          }

          .username-icon {
            img {
              width: 21px;
              height: 26px;
            }
          }

          .password-icon {
            img {
              width: 21px;
              height: 26px;
            }
          }

          /*忘记密码*/
          .fogotPW-btn-box {
            width: 387px;
            height: 50px;
            text-align: right;

            cursor: default;

            span {
              cursor: pointer;

              font-size: 16px;
              font-family: Microsoft YaHei;
              font-weight: 400;
              color: rgba(255, 255, 255, 1);
              line-height: 50px;
            }
          }

          /*登录按钮*/
          .login-btn-box {
            cursor: pointer;

            width: 274px;
            height: 42px;

            background: url("../../assets/images/login/new/登录按钮.png");

            span {
              font-size: 16px;
              font-family: Microsoft YaHei;
              font-weight: 400;
              color: rgba(255, 255, 255, 1);
            }
          }
        }

      }
    }


    /*页脚*/
    .footer {
      position: fixed;
      width: 100%;
      bottom: 0;
      margin: 0 auto;


      .copyright {
        font-size: 14px;
        color: #828282;
        text-align: center;
      }
    }


  }

  .max-img {
    width: 170px;
    height: auto;
  }


</style>



